.borderbox_wrap {
  background-color: #F5F5F5;
}
.userinfo {
  margin-top: 50px;
  margin-bottom: 100px;
}
.userinfo .userimg {
  padding-right: 150px;
}
.userinfo .userimg img {
  width: 273px;
  height: 349px;
}
.userinfo .usertext {
  width: 423px;
}
.userinfo .usertext .user_tit {
  font-size: 32px;
  font-weight: bold;
  padding-bottom: 39px;
  color: #27223E;
}
.userinfo .usertext .user_tit:after {
  content: '';
  display: inline-block;
  width: 40%;
  height: 0;
  border-top-style: solid;
  border-width: 2px;
  border-color: #27223E;
}
.userinfo .usertext .user_context {
  font-size: 20px;
  line-height: 2.3;
}
.userinfo .usertext .user_ctrl button {
  width: 142px;
  height: 40px;
  font-size: 14px;
  border-width: 2px;
}
.userinfo > div {
  display: table-cell;
  vertical-align: top;
}
.article_top .title {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  letter-spacing: 10px;
  margin-top: 50px;
}
.article_top .title:after {
  content: '';
  display: block;
  width: 10%;
  height: 0;
  border-width: 2px;
  border-top-style: solid;
  border-color: #000;
  margin: 20px auto;
}
.article_top .article_box_wrap {
  clear: both;
  overflow: hidden;
  padding-bottom: 50px;
}
.article_top .article_box_wrap > div {
  float: left;
}
.article_top .article_box_wrap .article_box {
  width: 290px;
  cursor: pointer;
  margin-left: 52px;
}
.article_top .article_box_wrap .article_box:first-child {
  margin: 0;
}
.article_top .img_wrap {
  width: 100%;
  height: 166px;
  overflow: hidden;
  position: relative;
}
.article_top .img_wrap img {
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 350px;
  height: 250px;
  display: block;
}
.article_top .art_info {
  padding: 0 15px;
  border: 1px solid #a6a8a5;
}
.article_top .art_tit:after {
  content: '';
  display: inline-block;
  width: 100%;
  height: 0;
  border-top-style: solid;
  border-width: 1px;
  border-color: #a6a8a5;
  margin: 20px 0;
}
.article_top .art_tit .titname {
  margin-top: 30px;
  height: 60px;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
}
.article_top .art_data {
  margin-bottom: 20px;
  font-size: 14px;
}



