@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  height: 100%;
  min-height: 100%;
  width: 100%;
  font: normal normal normal 14px/1.4em 'Microsoft YaHei', 'PingFang SC', sans-serif; }

/* 文章字体 */
.blog-post-page-font {
  font-size: 18px;
  line-height: normal;
  font: normal normal normal 14px/1.4em helvetica-w01-roman, helvetica-w02-roman, helvetica-lt-w10-roman, sans-serif; }

.center_borderbox {
  width: 980px;
  margin: 0 auto;
  overflow: hidden; }

/*粘连footer方法*/
.App,
.wrapper {
  min-height: 100%; }

/* end */
.App .footer {
  width: 100%;
  height: 113px; }

.rootwrap {
  width: 100%;
  height: 100%;
  position: relative;
  min-width: 1200px; }

#desktop_box {
  top: 0;
  background-color: gainsboro;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1; }

#bg_image {
  position: absolute;
  top: 0px;
  width: 100%;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100%;
  background-image: url("~@assets/img/beijing.png"); }

#site_root {
  position: relative;
  min-height: calc(100vh - 113px); }



