.banner {
  height: 316px;
  position: relative;
  overflow: hidden;
  background-color: #F5EFF6;
  background-image: url('~@assets/img/travel-youtube-banner.jpg');
  background-size: 100%;
  background-position: center;
}
.banner img {
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: block;
  width: 100%;
}



