.title {
  width: 100%;
  background-color: #27223E;
}
.title header {
  display: table;
  text-align: center;
}
.title a {
  display: table-cell;
  vertical-align: middle;
  height: 36px;
  line-height: 36px;
  text-decoration: none;
  color: #fff;
  width: 36px;
}
.title a.selected {
  color: #DFCFAE !important;
}



