.footer {
  background: #fff;
  color: #a5a5a5;
  overflow: hidden;
}
.footer .center_borderbox {
  display: table;
  margin-top: 30px;
}
.footer .center_borderbox > div {
  display: table-cell;
  vertical-align: middle;
}
.website_ico > div {
  display: table-cell;
  vertical-align: middle;
}
.website_ico > div:first-child:before {
  content: '';
  display: inline-block;
  width: 64px;
  height: 61px;
  background-image: url('~@assets/img/footLogo.png');
  background-repeat: no-repeat;
  background-size: 100%;
}
.share_team > span {
  display: inline-block;
  vertical-align: middle;
  width: 32px;
  height: 32px;
  background-size: 100%;
  margin: 0 5px;
  cursor: pointer;
}
.share_team .icon_zhihu {
  background-image: url('~@assets/img/icozhihu.png');
}
.share_team .icon_sf {
  background-image: url('~@assets/img/4097176351-5c4fc6084ff14_small.png');
}
.share_team .icon_git {
  background-image: url('~@assets/img/github.png');
}



